<template>
<div id="container">
    <h2>
        contact
    </h2>

    <div class="contact_wrap" >
        <nav>
            <a href="#" v-for="(e,x) in contacts" :key="x" @click.prevent="tabOpen(x)" v-bind:class="{On:x==cnt}">
                {{e.title}}
            </a>            
        </nav>

        <div class="contact_content" v-for="(e,x) in contacts" :key="x">
            <div v-if="cnt==x">
                <h4>
                    {{e.title}}
                </h4>
                <p>
                    {{e.call}}
                </p>
                <span class="material-icons">
                    {{e.icon}}
                </span>
                <button class="svc_btn">
                    {{e.title}}
                </button>
            </div>
        </div>

    </div>

</div>
</template>
<script>
import contact from '../assets/contact.js'

export default {
    data(){
        return{
            contacts:contact,
            cnt:0,
            On:''
        }
    },

    methods:{
        tabOpen(x){
            this.cnt=x
        }
    }
   
}
</script>
<style scoped>
    .contact_wrap nav {
    display: flex;
    justify-content: space-around;
    height: 5rem;
    align-items: center;
  }
  .contact_wrap nav a {
    display: block;
    height: 100%;
    width: 100%;
    line-height: 5rem;
    border-bottom: 3px solid rgb(224, 224, 224)
  }
  .contact_wrap nav a.On {
    background-color: rgb(244, 233, 255);
    color: rgb(97, 47, 143);
    border-bottom: 3px solid rgb(97, 47, 143)
  }
  .contact_content>div {
    height: 50rem;
    border: 1px solid rgb(224, 224, 224);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5rem;
  }
  .contact_content>div p {
    font-weight: 600;
    font-size: 3rem;
  }
  .contact_content>div h4 {
    font-size: 1.6rem;
    font-weight: 200;
  }
  .contact_content>div span {
    font-size: 10rem;
    display: inline-block;
    margin: 3rem 0;
    color: rgb(97, 47, 143)
  }
  .svc_btn {
    border: none;
    padding: .5rem 2rem;
    border-radius: 3rem;
    cursor: pointer;
  }
</style>