<template>
    <div id="container">
        <h2>티．라떼 List</h2>
        <div class="in_wrap">
                <div class="content" v-for="(c,i) in teas" :key="i">
                    <h3 @click="modalOpen(i)">{{c.title}}</h3>
                    <img @click="modalOpen(i)" :src="c.image" :alt="c.id">

                <Modal :modal="modal" :teas="teas" :cnt="cnt" @closeModal="modalClose"></Modal>
                </div>
        </div>

        <button class="more" @click="more()">더보기</button>

    </div>
</template>
<script>
import axios from 'axios';

import tea from '../assets/tea.js'
import Modal from './TeaModal'
export default {

    data(){
        return{
            teas:tea,
            modal:false,
            cnt:0,
            TeamoreList:0
        }
    },

    methods:{
        more(){
            axios.get(`https://baejeongyoon.github.io/AddSource/tea_moreList${this.TeamoreList}.json`)
            .then( result => {
                console.log(result.data);
                this.teas.push(result.data)
                this.TeamoreList++
                if(this.TeamoreList == 4) {
                  this.TeamoreList=0
                } 
            })
        },

        modalOpen(i){
            this.cnt=i
            this.modal=true
        },
        modalClose(){
            this.modal=false
        }
    },

    components:{
        Modal
    }
}
</script>
<style scoped>
    #container {
    margin-bottom: 10rem
    }
    .in_wrap {
    justify-content: space-around;
    display: flex;
    flex-wrap: wrap;
    cursor: pointer;
    }
    img {
    /* width: auto; */
    height: auto;
    }
    h3 {
    color: gray;
    font-size: 1.6rem;
    margin: 3rem 0 1rem;
    position: relative
    }
    h3:after {
    display: block;
    content: "";
    width: 10%;
    height: .5rem;
    background-color: gainsboro;
    margin: 1rem auto
    }

    .more {
    border: none;
    color: rgb(77, 77, 77);
    border-radius: 2rem;
    padding: 0 1rem;
    display: inline-block;
    margin-top: 3rem;
    line-height: 3rem;
    cursor: pointer;
  }
</style>
