import { createWebHistory, createRouter } from "vue-router";
import HollyMain from './components/HollyMain.vue';
import HollyCoffee from './components/HollyCoffee.vue';
import HollyTea from './components/HollyTea.vue';
import HollyFood from './components/HollyFood.vue';
import HollyMD from './components/HollyMD.vue';
import HollyContact from './components/HollyContact.vue';

const routes = [
    {path: "/", component: HollyMain, },   
 {path: "/HollyCoffee", component: HollyCoffee, },
 {path: "/HollyTea", component: HollyTea, },
 {path: "/HollyFood", component: HollyFood, },
 {path: "/HollyMD", component: HollyMD, },
 {path: "/HollyContact", component: HollyContact, },

];
const router = createRouter({
 history: createWebHistory(), routes,
});
export default router;